@font-face {
  src:
    local("Jeko-Regular"),
    url("../fonts/Jeko-Regular.woff2") format("woff2"),
    url("../fonts/Jeko-Regular.woff") format("woff");
  font-family: "Jeko-Regular";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
